// Annual receipts callout with calendar icon.
.give-annual-callout {
	background-color: #fcfcfc;
	border: 1px solid #e0e0e0;
	display: flex;
	padding: 10px;

	&__icon {
		background-image: url("../../images/calendar-icon.png");
		background-repeat: no-repeat;
		background-size: cover;
		flex-basis: 24px;
		height: 24px;
		margin-right: 10px;
		opacity: 0.6;
	}

	&__text {
		flex: 1;
	}
}

// Vertical list with bullets used for PDF navigation.
.give-bullet-nav {
	list-style: disc;
	margin: 10px 0 20px 20px;
	padding: 0;
}

// Horizontal navigation for browsing donations by year.
.give-inline-nav {
	display: inline-block;
	list-style: none;
	margin: 0 0 10px;
	padding: 0;

	&__item {
		border-right: 1px solid #e0e0e0;
		display: inline-block;
		margin: 0;
		padding: 0;

		a {
			border: 0 !important;
			box-shadow: none !important;
			display: inline-block;
			padding: 0 10px;

			&:hover,
			&:focus,
			&:active {
				text-decoration: underline;
			}
		}

		&--active a {
			font-weight: bold;
			text-decoration: underline;
		}

		&:last-child {
			border: 0;
		}
	}
}

// Vertical list of donations with thumbnail and donation summary.
.give-annual-donations {
	border: 1px solid #e0e0e0;
	line-height: 1.3;
	list-style: none;
	margin: 0 0 20px;
	padding: 0;

	&__item {
		background-color: #fcfcfc;
		border-bottom: 1px solid #e0e0e0;
		display: flex;
		padding: 20px;

		&:nth-child(even) {
			background-color: #fff;
		}

		&:last-child {
			border: 0;
		}
	}

	&__media {
		flex-basis: 60px;
		margin-right: 20px;
		padding-top: 4px;

		img {
			display: block;
			height: auto;
			margin: 0 !important;
			width: 100%;
		}
	}

	&__placeholder-image {
		opacity: 0.6;
	}

	&__body {
		flex: 1;
	}

	&__form-title {
		font-size: 1.125em;
	}

	&__summary {
		hyphens: none;
		margin: 0;
		padding: 0;
	}

	&__meta {
		font-size: 0.875em;
	}
}



